import { useRef, useState } from 'react'
import { useSpring, animated, interpolate } from 'react-spring'
import theme from '../../theme'

const StandardInput = () => {
  const [isSpring, setIsSpring] = useState(false)
  const { o, color } = useSpring({
    from: { o: 0, color: theme.palette.shade[30] },
    o: isSpring ? 1 : 0,
    color: isSpring ? 'green' : theme.palette.shade[30]
  })
  return (
    <>
      <animated.input
        type="text"
        style={{
          border: interpolate([o, color], (o, c) => `${o * 1}px solid ${c}`),
          outline: 'none',
          borderRadius: '4px',
          padding: `${theme.spacing}px ${theme.spacing * 2}px`
        }}
        onFocus={() => setIsSpring(true)}
        onBlur={() => setIsSpring(false)}
      />
      <style jsx>{`
        input {
          border-radius: 4px;
        }
      `}</style>
    </>
  )
}

export default StandardInput
